<template>
  <div>
    <h2>&#128150; {{isLogin?'ng Nhp':'ng K'}} &#128150;</h2>
    <form @submit.prevent v-if="isLogin">
      <table>
        <tr>
          <td>Tn Ti Khon &nbsp;</td>
          <td>
            <input type="text" v-model="account.username" placeholder="..." />
          </td>
        </tr>
        <tr>
          <td>Mt Khu &nbsp;</td>
          <td>
            <input type="password" v-model="account.password" placeholder="..." />
          </td>
        </tr>
      </table>
      <button @click="onSubmit(account)">ng Nhp</button>
      <button @click="isLogin=false">ng K</button>
    </form>
    <form @submit.prevent v-else>
      <table>
        <tr>
          <td>H v Tn &nbsp;</td>
          <td>
            <input type="text" v-model="account.fullName" placeholder="..." />
          </td>
        </tr>
        <tr>
          <td>Tn ti Khon &nbsp;</td>
          <td>
            <input type="text" v-model="account.username" placeholder="..." />
          </td>
        </tr>
        <tr>
          <td>Mt Khu &nbsp;</td>
          <td>
            <input type="password" v-model="account.password" placeholder="..." />
          </td>
        </tr>
      </table>
      <button @click="onSubmit(account)">Register</button>
      <button @click="isLogin=true">Cancel</button>
    </form>
  </div>
</template>
<script>
import TODO from "../requester/todo";
import router from "vue-router";
const app = document.querySelector("#app");

export default {
  data() {
    return {
      isLogin: true,
      account: {},
    };
  },
  methods: {
    onLoad(isLoad) {
      isLoad ? app.classList.add("loadding") : app.classList.remove("loadding");
    },
    onSubmit(user) {
      if (user.username === "" || user.password === "") {
        return alert("Vui Lng Nhp y  Thng Tin");
      }
      this.onLoad(true);
      if (this.isLogin) {
        // login
        TODO.login(user)
          .then((dt) => {
            const { result, accessToken } = dt.data;
            if (result === "success") {
              localStorage.setItem("auth", accessToken);
              this.$router.push({ path: "/" });
            }
            if (result === "error") {
              alert("Ti Khon hoc mt khu khng ng");
            }
            this.onLoad(false);
          })
          .catch((err) => console.error(err));
      } else {
        if (user.fullName === "") {
          return alert("Vui Lng Nhp Tn");
        }
        // register
        TODO.register(user)
          .then((dt) => {
            const { result, accessToken } = dt.data;
            if (result === "success") {
              localStorage.setItem("auth", accessToken);
              this.$router.push({ path: "/" });
            } else alert(result);
            this.onLoad(false);
          })
          .catch((err) => console.error(err));
      }

      this.account = {};
    },
  },
  created() {
    this.onLoad(false);

    const auth = localStorage.getItem("auth");
    if (auth && auth !== "") {
      this.$router.push({ path: "/" });
    }
  },
};
</script>

<style scoped>
h1,
h2 {
  font-weight: normal;
  color: #42b983;
}
table {
  margin: 5px auto;
}
table * {
  font-size: 1rem;
  letter-spacing: 1px;
}
table input {
  padding: 2.5px 0 2.5px 8px;
  margin: 3px 0;
  border-radius: 4px;
  border: thin solid #296047;
  background: none;
  color: #42b983;
}
table tr td:first-child {
  text-align: left;
  padding: 4px 0;
}
</style>



