import { GraphRequester } from './BaseRequester'
import axios from 'axios'
class TodoRequester extends GraphRequester {
  constructor() {
    super('todo')
  }
  async login(data) {
    this.setConfig()
    return await axios.post(this.path + 'login', data, this.config)
  }
  async register(data) {
    this.setConfig()
    return await axios.post(this.path + 'register', data, this.config)
  }
  async addMsg(data) {
    this.setConfig()
    return await axios.post(this.path + 'addMsg', data, this.config)
  }
  async removeMsg(data) {
    this.setConfig()
    return await axios.post(this.path + 'removeMsg', data, this.config)
  }
}
export default new TodoRequester()


