import axios from 'axios'

export class GraphRequester {
  constructor(path) {
    // this.path = 'http://localhost:8080/' + path + '/'
    this.path = 'https://app-46543b07-877e-48f6-a9b6-cd815770a410.cleverapps.io/' + path + '/'
    this.config = { headers: { Authorization: '' } }
  }
  getAll(data = {}) {
    this.setConfig()
    return axios.post(this.path + 'getAll', data, this.config)
  }
  get(_id, opts = {}) {
    this.setConfig()
    return axios.post(this.path + 'get', { ...opts, _id }, this.config)
  }
  create(data) {
    this.setConfig()
    return axios.post(this.path + 'create', data, this.config)
  }
  update(_id, opts = {}) {
    this.setConfig()
    return axios.post(this.path + 'update', { ...opts, _id }, this.config)
  }
  delete(_id, opts = {}) {
    this.setConfig()
    return axios.post(this.path + 'delete', { ...opts, _id }, this.config)
  }
  getUser(id) {
    this.setConfig()
    return axios.post(this.path + 'get', id, this.config)
  }
  setConfig() {
    this.config.headers['Authorization'] = String(localStorage.getItem('auth'));
  }
}



